body
{
    margin: 0;
    padding: 0%;
    /* background-color: yellowgreen; */
    /* background-image: url("./Inspirational\ Gradient\ Desktop\ Background\ \(1\).png"); */
    
}

.root
{
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
    /* background-image: url("./Inspirational\ Gradient\ Desktop\ Background\ \(1\).png"); */
    

}
.lets 
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.lets_start
{
    display: grid;
    grid-template-rows:1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    width: min-content;

}


.img_bg
{
    width: 150px;
    height: 150px;
}


.heading
{
    display: flex;
    justify-content: center;
    align-items: center;
   
        text-transform: uppercase;
        background: linear-gradient(to right, #30CFD0 0%, #23024b 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin: 0;
    padding: 0;
       
}

.reset
{
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 50px;
    
   
}

.resett:hover
{
    background-color: black;
    color: white;
   
}
.won
{
    background-image: url("./won_bg.png");
    background-repeat: no-repeat;
    background-color: yellow;
    padding: 0;
    margin:0 ;
    height: 100vh;
}


